
<template>
  <div id="app">
    <el-scrollbar
      ref="scrollbar"
      class="sc-scrollbar"
      wrapClass="sc-warp"
      viewClass="sc-view"
    >
      <div class="layout">
        <Sider :style="{position: 'fixed', height: '100vh', left: 0, overflow: 'auto'}">
          <Menu
            :active-name="actived"
            theme="dark"
            width="auto"
            :open-names="[]"
          >
            <Submenu name="1">
              <template slot="title">
                <Icon type="ios-navigate"></Icon>element表单校验
              </template>
              <MenuItem
                v-for="item in validateMenuList"
                :key="item.name"
                :name="item.name"
                :to="{name: item.router}"
              >{{item.title}}</MenuItem>
            </Submenu>
            <Submenu name="2">
              <template slot="title">
                <Icon type="logo-chrome" />css测试
              </template>
              <MenuItem
                v-for="item in cssMunuList"
                :key="item.name"
                :name="item.name"
                :to="{name: item.router}"
              >{{item.title}}</MenuItem>
            </Submenu>
            <Submenu name="3">
              <template slot="title">
                <Icon type="md-briefcase" />webpack测试
              </template>
              <MenuItem
                v-for="item in webpackList"
                :key="item.name"
                :name="item.name"
                :to="{name: item.router}"
              >{{item.title}}</MenuItem>
            </Submenu>
            <Submenu name="4">
              <template slot="title">
                <Icon type="md-bonfire" />vue 使用
              </template>
              <MenuItem
                v-for="item in vueSlotList"
                :key="item.name"
                :name="item.name"
                :to="{name: item.router}"
              >{{item.title}}</MenuItem>
            </Submenu>
          </Menu>
        </Sider>
        <Layout :style="{marginLeft: '200px'}">
          <Header :style="{background: '#fff', boxShadow: '0 2px 3px 2px rgba(0,0,0,.1)'}">
            <h1>{{handleTilte()}}</h1>
          </Header>
          <Content :style="{padding: '0 16px 16px'}">
            <Breadcrumb :style="{margin: '16px 0'}">
              <BreadcrumbItem to="/">Home</BreadcrumbItem>
              <BreadcrumbItem>{{titleName}}</BreadcrumbItem>
            </Breadcrumb>
            <Card>
              <div class="sc-container">
                <router-view></router-view>
              </div>
            </Card>
          </Content>
        </Layout>
      </div>
    </el-scrollbar>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  computed: {
    ...mapState({
      validateMenuList: state => state.validateMenuList,
      cssMunuList: state => state.cssMunuList,
      titleType: state => state.titleType,
      webpackList: state => state.webpackList,
      vueSlotList: state => state.vueSlotList
    }),
    titleName () {
      return this.$route.matched.length === 1 ? this.$route.matched[0].meta.title : ''
    },
    actived () {
      let activeRouter = ''
      const MENU_LIST = [...this.validateMenuList, ...this.cssMunuList, ...this.webpackList, ...this.vueSlotList]
      MENU_LIST.map(item => {
        if (this.$route.name === item.router) {
          activeRouter = item.name
        }
      })
      return activeRouter
    }
  },
  mounted () {
    /* 窗口变化修改滚动条高度 */
    window.onresize = () => {
      this.$refs.scrollbar.update()
    }
  },
  methods: {
    /**
     * @description: 计算title显示
     * @return: string title
     */
    handleTilte () {
      if (!this.actived) return ''
      const N = this.actived.slice(0, 1)
      return this.titleType.filter(item => {
        return item.type === N
      })[0].title
    }
  }
}
</script>

<style lang="less">
@import url('~@/less/base.less');
html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}
#app {
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.sc-scrollbar {
  height: 100%;
  .el-scrollbar__wrap {
    overflow-x: hidden;
  }
  .sc-warp {
    // 父盒子
    // background-color: red;
  }
  .sc-view {
    // 子盒子
    // background-color: blue;
  }
  // is-vertical 垂直滚动条 可以设置宽度以及背景色
  .el-scrollbar__bar.is-vertical {
    width: 8px;
    .el-scrollbar__thumb {
      background-color: #e57300;
    }
  }
}
.layout {
  // border: 1px solid #d7dde4;
  background: #f5f7f9;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}
.layout-header-bar {
  background: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}
.sc-container {
  min-height: 600px;
}
</style>
